export default {
	data() {
		return {
			globalData: {},
			params: {},
			details: {},
			balance: 0,
			isSwitch: false,
			remark: ''
		};
	},
	onLoad: function(option) {
		this.globalData = getApp().globalData;
		this.loadData(option.id);
	},
	methods: {
		loadData(id) {
			this.$minApi
				.orderDetails({ id: id })
				.then(res => {
					this.details = res.data;
				})
				.catch(err => {
					console.log(err);
				});
		},
		doPay() {
			this.$minApi
				.orderPay({
					order_id: this.details.id,
				})
				.then(res => {
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: String(Date.now()),
						nonceStr: res.nonce_str,
						package: 'prepay_id=' + res.prepay_id,
						signType: 'MD5',
						paySign: res.sign,
						success: function(res) {
							console.log('success:' + JSON.stringify(res));
						},
						fail: function(err) {
							console.log('fail:' + JSON.stringify(err));
						}
					});
					uni.redirectTo({
						url: '/pages/order/details?id=' + res.order_id
					});
				})
				.catch(err => {
					console.log(err);
				});
		},
		editStatus(status) {
			this.$minApi
				.orderEdit({ id: this.details.id, status: status })
				.then(res => {
					this.details = res.data;
				})
				.catch(err => {
					console.log(err);
				});
		},
		toComment() {
			uni.navigateTo({
				url: '/pages/order/comment?order_id' + this.details.id
			});
		},
		toGoodsDetails(id) {
			uni.navigateTo({
				url: '/pages/goods/details?id=' + id + '&keyword=' + this.globalData.userInfo.keyword
			});
		}
	}
};
